___CSS_LOADER_EXPORT___.push([module.id, `.our-services-section {
    background-color: #f0f0f0; /* Matches your existing background */
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 40px 20px;
    margin: 0 auto;
  }
  
  /* Section Title (already present in your code) */
  .hero-content-title {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
  
  .line {
    /* inline styles for color/width/height are in the component */
  }
  
  .company-name {
    /* inline styles for color, fontSize, fontWeight are in the component */
  }
  
  /* Slogan styling */
  .our-services-slogan {
    font-size: 34px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
      Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bolder;
    color: rgb(35, 53, 51);
    line-height: 40px;
    margin: 15px auto;
    text-align: center;
    padding: 5px 0;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  }
  
 /* Remove default button styles and allow full-width clickable card */
.service-card {
    display: block;
    width: 100%;
    text-align: left;
    border: none;
    background: none;
    padding: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  /* Optional: add a border or shadow to separate cards visually */
  .service-card {
    background-color: #fff;
    border-radius: 8px;
    width: 90%;
    max-width: 400px;
    text-align: center;
    padding: 20px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.08),
                0 4px 6px rgba(0, 0, 0, 0.04);
    transition: all 0.5s ease-in;
    margin: 0 auto; /* Centers the card if your parent isn't controlling layout */
  }
  /* Hover effect: change background to match the link color */
  .service-card:hover {
    background-color: rgb(37, 54, 53);
    color: #fff; /* Adjust text color for contrast */
  }
  
  /* Ensure child elements inherit color changes if needed */
  .service-card:hover .service-title,
  .service-card:hover .service-description {
    color: #fff;
  }
  
  /* Remove any default focus outline for accessibility if desired, but consider a custom focus style */
  .service-card:focus {
    outline: none;
    box-shadow: 0 0 0 2px rgba(87, 128, 124, 0.5);
  }
  
  /* Circular Image */
  .service-image {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    margin: 20px;
  }
  
  /* Service Title */
  .service-title {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
                 Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1.4rem;
    font-weight: 700;
    color: #333;
    margin: 0 0 10px 0;
  }
  
  /* Service Description */
  .service-description {
    font-size: 0.95rem;
    color: #555;
    line-height: 1.5;
    margin-bottom: 15px;
  }
  
  /* "Learn More" Link */
  .service-link {
    display: inline-block;
    margin-top: 15px;
    font-size: 0.9rem;
    color: rgb(37, 54, 53);
    text-decoration-line: underline;
    font-weight: 600;
  }
  
  .service-link:hover {
    text-decoration: underline;
  }
